    border: 0;
    padding: 0;
    font-style: normal;
}
html,
body {
    color: #333;
    font-family: 'Avenir', Helvetica, Arial, sans-serif, "Comic Sans MS", "hakuyoxingshu7000", "\65B9\6B63\5170\4EAD\8D85\7EC6\9ED1\7B80\4F53", "\53F6\6839\53CB\6BDB\7B14\884C\4E66  2.0\7248";
