body {
    /* max-width: 7.5rem; */
    margin: 0 auto;
    background-color: #fff;
}

.index{
    color:skyblue;
}
.index img{
    width:.3rem;
}
.main{
    margin-top: 1rem;
    margin-bottom: .8rem;
}
.movieType{
    padding: .2rem ;
    color: #333;
    font-size: .3rem;
}
.movieType span{
    padding:.1rem 0;
    border-bottom: .05rem solid pink;
}

.head{
    width:100%;
    height:1rem;
    line-height:1rem;
    font-size:.3rem;
    padding:0 .36rem; 
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(250, 255, 173, 0.8)),color-stop(rgba(242, 255, 34, 0.5)),to(rgba(250, 255, 173, 0.8))); 
    background: linear-gradient(to bottom,rgba(250, 255, 173, 0.8),rgba(242, 255, 34, 0.5),rgba(250, 255, 173, 0.8)); 
    color:#fb00c3;
    position:fixed;
    top:0;
    left:50%;
    margin-left:-50%;
    z-index:99;
}
.head:after,.head:before{
    content:"";
    display:block;
    clear:both;
}
.head .title{
    font-family:"hakuyoxingshu7000";
    float:left;
    font-size:.5rem;
}
.head .search{
    float:left;
    height:100%;
    margin-left:.8rem;
    overflow:hidden;
}
.head .search .searchInp{
    float:left;
    width:3rem;
    background-color:rgba(255,255,255,.8);
    height:.4rem;
    margin:.3rem 0;
    font-size:.2rem;
    border-radius:.25rem;
}
.head .search .searchInp input{
    padding:0 .2rem;
}
.head .search .searchBtn{
    float:left;
    height:.4rem;
    width:.8rem;
    font-size:.24rem;
    margin:.3rem 0;
    margin-left:.1rem;
    color:#fff;
    border-radius:.1rem;
    cursor:pointer;
}
.head .headNav{
    float:right;
    overflow:hidden;
    font-size:.2rem;
}
.head .headNav .headNavLi{
     float:left;
     padding:0 .1rem;
     margin:0 .2rem;
}
.head .headNav .headNavLi a{
    color:#999;
}
.head .headNav .headNavLi a:hover{
    border-bottom:1px solid skyblue;
}
.mobileNavBox{
    float:right;
    width:.5rem;
    height:1rem;
    position:relative;
}
.mobileNavIconbox{
    width:.5rem;
    height:.5rem;
    margin:.25rem 0;
    position:relative;
}
.mobileNavIconbox .mobileNavIcon{
    display:block;
    width:.5rem;
    height:.1rem;
    position:absolute;
    left:0;
    border-radius:.05rem;
    background-color:#fff;
    -webkit-transition:all .3s;
    transition:all .3s;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
}
.mobileNavIconbox .mobileNavIcon:nth-of-type(1){
    top:0;
}
.mobileNavIconbox .mobileNavIcon:nth-of-type(2){
    top:.2rem;
}
.mobileNavIconbox .mobileNavIcon:nth-of-type(3){
    top:.4rem;
}
.mobileNavIconbox.open .mobileNavIcon:nth-of-type(1){
    top:.2rem;
    -webkit-transform:rotateZ(45deg);
            transform:rotateZ(45deg);
}
.mobileNavIconbox.open .mobileNavIcon:nth-of-type(2){
    display:none;
}
.mobileNavIconbox.open .mobileNavIcon:nth-of-type(3){
    top:.2rem;
    -webkit-transform:rotateZ(-45deg);
            transform:rotateZ(-45deg);
}
.mobileNav{
    width:3rem;
    text-align:center;
    position:absolute;
    right:-.35rem;
    top:1rem;
    z-index:9;
}
.mobileNav .mobileNavLi{
    height:.8rem;
    line-height:.8rem;
    background-color: rgba(251, 0, 195,.3);
    color: #fff;
    border-bottom: 2px solid transparent;
    border-top: 2px solid transparent;
}
.bounce-enter-active{
    -webkit-animation: bounce-in .5s;
            animation: bounce-in .5s;
}
.bounce-leave-active {
    animation: bounce-in .5s reverse;
}
@-webkit-keyframes bounce-in {
0% {
    opacity:0;
    top:-.5rem;
}
50% {
    opacity:0;
    top:.5rem;
}
100% {
    opacity:1;
    top:1rem;
}
}
@keyframes bounce-in {
0% {
    opacity:0;
    top:-.5rem;
}
50% {
    opacity:0;
    top:.5rem;
}
100% {
    opacity:1;
    top:1rem;
}
}

.foot{
    width: 100%;
    height: .8rem;
    line-height: .8rem;
    position: fixed;
    left: 50%;
    bottom: 0;
    margin-left: -50%;
    background-color:rgba(250, 255, 173, 0.6);
    color:#fb00c3;
    text-align: center;
}

.hot {
  width: 12rem;
  margin: 0 auto;
  overflow: hidden;
}
.hot.hotMobile {
  width: 7.5rem;
}
.movieClass {
  float: left;
  width: 2.6rem;
  margin: 0.2rem;
  overflow: hidden;
}
.movieClass.mobileMovieClass {
  width: 2.1rem;
}
.movieClass .poster {
  width: 100%;
  height: 4rem;
}
.movieClass.mobileMovieClass .poster {
  width: 100%;
  height: 3rem;
}
.poster img {
  width: 100%;
  height: 100%;
}
.movieClass .title {
  font-size: 0.22rem;
  text-align: center;
  height: 0.56rem;
  line-height: 0.28rem;
  margin: 0.1rem 0;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  color: #333;
}
.hot .stars {
  height: 0.4rem;
  line-height: 0.4rem;
  min-width: 1rem;
  text-align: center;
}
.hot .stars>div{
  height: 0.4rem;
  width: 1.85rem;
  line-height: 0.4rem;
  margin: 0 auto;
  overflow: hidden;
}
.hot .stars .starnum{
  float: left;
  margin-right: .1rem;
}
.hot .yd-rate{
  float: right;
}
.hot .yd-cell-left {
  width: 100%;
  height: 100%;
}
.hot .yd-rate a,
.hot .rate-active {
  width: 20%;
  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.28rem;
  padding: 0 !important;
}
.hot .yd-rate a:after {
  width: 100%;
  height: 100%;
}


.movieDetails{
    padding:1rem 0;
}
.movieMsg{
    width: 7.5rem;
    padding: .25rem;
    overflow: hidden;
}
.photoScore,.introduce{
    float: left;
}
.photoScore{
    width: 2rem;
}
.photoScore img{
    display: block;
    width: 100%;
    height: 100%;
}
.introduce{
    margin-left: .2rem;
    color:#999;
}
.movieName{
    font-size: .3rem;
    color: #666;
}
.protagonist,.type{
    overflow: hidden;
    width: 4.8rem;
}
.protagonist .title,.type .title{float: left;
}
.protagonist .li,.type .li{float: left;
}
.protagonist .li span::before,.type .li span::before{
    content: " / ";
    color: #333;
}
.protagonist .li span,.type .li span{
    color: skyblue;
    padding-left: .05rem;
}
.protagonist .li:nth-of-type(2) span::before,.type .li:nth-of-type(2) span::before{
    content: "";
}
.scorebox{
    padding:0 .2rem;
    height: 1.5rem;
    overflow: hidden;
}
.scorebox .score{
    float: left;
    width: 3.2rem;
    background-color: #eee;
    padding: .1rem;
    border-radius: .1rem;
}
.scorebox .score .cont{
    overflow: hidden;
    height: 1rem;
}
.averagenumber{
    float: left;
    width: 1rem;
    line-height: 1rem;
    font-size: .4rem;
    text-align: center;
    color: red;
}
.averagestar{
    float: left;
    width: 2rem;
}
.averagestar .yd-rate{
  font-size: 0.28rem;
  width: 1.5rem;
  margin: .1rem auto;
}
.averagestar .yd-cell-left {
  width: 100%;
  height: 100%;
}
.averagestar .yd-rate a,
.averagestar .rate-active {
  width: 20%;
  height: 0.4rem;
  line-height: 0.4rem;
  padding: 0 !important;
  font-size: .3rem;
}
.averagestar .stars {
  height: 0.3rem;
  width: 100%;
  text-align: center;
}
.scorebox .score .cont .commentNumber{
    font-size:.24rem;
    width: 100%;
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}
.starDetails{
    float: left;
    width:3.8rem;
    height: 1.5rem;
    padding: 0 .3rem;
}
.starDetails .li{
    position: relative;
    height: .3rem;
    line-height: .26rem;
    padding: .02rem 0;
    padding-left: .8rem;
    font-size: .2rem;
}
.starDetails .li span{
    position: absolute;
    left: 0;
    top: .05rem;
    width: .8rem;
}
.progress{
    width: 100%;
    height:100%;
    border-radius: .13rem;
    overflow: hidden;
    border: .01rem solid #eee;
    position: relative;
}
.progress .width{
    width: 0%;
    height: 100%;
    border-radius: .12rem;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    background-color: pink;
}
.fade-enter-active {
  -webkit-transition: all .5s ease;
  transition: all .5s ease;
}
.fade-leave-active {
  -webkit-transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  transition: all .5s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.fade-enter, .fade-leave-to{
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
.progress .num{
    width: 100%;
    height: 100%;
    font-size: .2rem;
    position: absolute;
    left: 0;
    top: 0;
    text-align: center;
}
.short{
    padding: .2rem;
    background-color: #5b9600;
    color: #fff;
    margin: .2rem 0;
}
.short h3{
    font-size: .28rem;
    margin-bottom: .1rem;
}
.short p{
    line-height: .36rem;
}
.banner{
    height: 3rem;
    text-align: center;
}
.banner .swiper-slide img,.banner .swiper-slide .photo{
    display: block;
    width: 1.5rem;
    height: 2rem;
    margin: 0 auto;
    margin-bottom: .2rem;
}
.banner .swiper-slide img,.banner .swiper-slide .title{
    font-size: .2rem;
    color: #999;
}
.video{
    margin: .2rem 0;
    height: 4rem;
}
.video .banner{
    height: 4rem;
}
video{
    display: block;
    width: 100%;
    height: 100%;
    -o-object-fit: fill;
       object-fit: fill;
}
video::-internal-media-controls-download-button {
    display: none;
}
video::-webkit-media-controls-enclosure {
    overflow: hidden;
}
.shortComment .li{
    padding: .1rem .2rem;
}
.shortComment .li .title{
    line-height: .4rem;
}
.shortComment .li .title span{
    color:#5b9600;
}
